import React from "react";
// react plugin used to create charts
import { Line } from "react-chartjs-2";
import { Link } from "react-router-dom";

// reactstrap components
import { Card, CardHeader, CardBody, CardTitle, Row, Col } from "reactstrap";

// core components
import chartExample2 from "variables/charts.jsx";
import ReactPlayer from "react-player";

class Home extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			bigChartData: "data1"
		};
	}
	setBgChartData = name => {
		this.setState({
			bigChartData: name
		});
	};

	render() {
		return (
			<>
				<div className='content'>
					<Row>
						<Col sm='12'>
							<Card>
								<CardBody>
									<Row className='justify-content-center'>
										<Col lg='6' sm='12'>
											<ReactPlayer
												url='https://www.youtube.com/watch?v=QmIM24JDE3A'
												controls
												className='mt-4'
												width='parent'
												style={{
													border: "0.3em solid #e14eca",
													borderRadius: "1em",
													padding: "0.5em"
												}}
											/>
										</Col>
									</Row>
									<Row>
										<Col sm='12' className='mt-3'>
											<h3 className='text-justify ml-4 mr-4'>
												What are we solving?
											</h3>
											<p className='text-justify ml-4 mr-4'>
												We develop an algorithm that can detect pneumonia from chest X-rays at a level 
												exceeding practicing radiologists.
												Chest X-rays are currently the best available method for diagnosing pneumonia, 
												playing a crucial role in clinical care and epidemiological studies. 
												Pneumonia is responsible for more than 1 million hospitalizations 
												and 50,000 deaths per year in the US alone.
											</p>
										</Col>
									</Row>
								</CardBody>
							</Card>
						</Col>
						<Col sm='12'>
							<Card>
								<CardBody>
									<Row>
										<Col sm='12' className='mt-5'>
											<h3 className='text-justify ml-4 mr-4'>Our Approach</h3>
											<p className='text-justify ml-4 mr-4'>
												Our model, CheXNet, is a 121-layer convolutional neural network that inputs a 
												chest X-ray image and outputs the probability of pneumonia along with a heatmap 
												localizing the areas of the image most indicative of pneumonia.
												We train CheXNet on the recently released ChestX-ray14 dataset, which contains 
												112,120 frontal-view chest X-ray images individually labeled with up to 14 
												different thoracic diseases, including pneumonia. We use dense connections
												and batch normalization to make the optimization of such a deep network tractable.
											</p>
											<p className='text-justify ml-4 mr-4'>
												The models are deployed on the flask-based web-app which
												consists of ReactJS (front end) interface. It is the
												most efficient and economical solution for implementing
												it. It works on the principle “Train once, use
												anywhere”. These models can also be extended in the
												future to be implemented as a API service for third
												party applications.
												<br />
												<br />
												Technology Stack:
												<ol>
													<li> Tensorflow / Keras / OpenCV</li>
													<li> Python, Flask (Open Source Web Framework)</li>
													<li> Google Cloud Platform and/or Microsoft Azure</li>
													<li> ReactJS</li>
												</ol>
											</p>
										</Col>
									</Row>
									<Row className='justify-content-center'>
										<Col lg='6' sm='12'>
											<ReactPlayer
												url='https://www.youtube.com/watch?v=Gigd1rkZTSE'
												controls
												className=''
												width='parent'
												style={{
													border: "0.3em solid #e14eca",
													borderRadius: "1em",
													padding: "0.5em"
												}}
											/>
										</Col>
									</Row>
								</CardBody>
							</Card>
						</Col>
						<Col lg='8'>
							<Card className='card-chart'>
								<CardHeader>
									<CardTitle tag='h3'>Performance History</CardTitle>
								</CardHeader>
								<CardBody>
									<div className='chart-area'>
										<Line
											data={chartExample2.data}
											options={chartExample2.options}
										/>
									</div>
								</CardBody>
							</Card>
						</Col>
						<Col lg='4' className='mt-auto mb-auto'>
							<Row className='justify-content-center'>
								<Link className='btn-simple btn-primary btn' to='classify'>
									Start Classifying
								</Link>
								{/* <Button className=' '></Button> */}
							</Row>
						</Col>
					</Row>
				</div>
				{/* ---------------------------- */}
			</>
		);
	}
}

export default Home;
