import React from "react";

// reactstrap components
import { Button, Card, CardBody, CardFooter, Row, Col } from "reactstrap";

class About extends React.Component {
	render() {
		return (
			<>
				<div className='content'>
					<Row>
						<Col md='4'>
							<Card className='card-user'>
								<CardBody>
									<div className='author'>
										<img
											alt='parag'
											src={require("assets/img/parag.jpeg")}
											className='avatar'
										/>
										<h5 className='title'>Parag Ghorpade</h5>
										<p className='description'>MACHINE-LEARNING</p>
									</div>
								</CardBody>
								<CardFooter>
									<div className='button-container'>
										<a
											href='https://github.com/Parag0506'
											target='_blank'>
											<Button className='btn-icon btn-round'>
												<i className='fab fa-github' />
											</Button>
										</a>
										<a
											href='https://www.linkedin.com/in/parag-ghorpade'
											target='_blank'>
											<Button className='btn-icon btn-round'>
												<i className='fab fa-linkedin' />
											</Button>
										</a>
									</div>
								</CardFooter>
							</Card>
						</Col>
					</Row>
				</div>
			</>
		);
	}
}

export default About;
