import React from "react";

// reactstrap components
import {
	Card,
	CardHeader,
	CardBody,
	Row,
	Col,
	CardTitle,
	CardText,
	Button
} from "reactstrap";

class Result extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (
			<div className='content'>
				<Row>
					<Col md='12'>
						<Row tag='h3' className='ml-2'>
							Results
						</Row>
						<Row>
							<Col>
								<Card>
									<CardHeader>
										<CardTitle tag='h4'></CardTitle>
									</CardHeader>
									<CardBody>
										<CardText tag='center'>
											<img src={`data:image/jpeg;base64,${this.props.image}`}></img>
										</CardText>
									</CardBody>
								</Card>
							</Col>
						</Row>
					</Col>
				</Row>
			</div>
		);
	}
}

export default Result;
